export const USER_NAME_MIN_LENGTH = 2;
export const USER_NAME_MAX_LENGTH = 60;


export function isUserName(value: unknown): boolean {
  const re = /^[a-zA-Z0-9_]+$/iu;

  return (
    typeof value === 'string' &&
    value.length >= USER_NAME_MIN_LENGTH &&
    value.length <= USER_NAME_MAX_LENGTH &&
    re.test(value)
  );
}
