import { type Dispatch, type SetStateAction } from 'react';

import { type BasicAppResources } from 'src/code.client/app_resources/basic_app_resources';

import { type DXTErrorCallback } from 'src/domain/shared/errors';
import { userLoginRequest } from 'src/api-client/auth';
import { type DXTApiRequestState } from 'src/api-client/shared/DXTApiRequestState';

import {
  type AuthState,
  AuthStateDisconnected,
  AuthStateLoading,
  AuthStateLoggedIn,
} from '../states';
import { AuthAction, type OnRetryCallBack } from './base';
import { type LoginUserInput } from 'src/domain/auth/inputs';
import { type AuthInfoResult } from 'src/domain/auth/results';

export class AuthActionLogin extends AuthAction {
  constructor(
    readonly params: LoginUserInput,
    readonly errorCallback?: DXTErrorCallback
  ) {
    super();
  }

  async run(
    authState: AuthState,
    setAuthState: Dispatch<SetStateAction<AuthState>>,
    app: BasicAppResources,
    onRetryCallback: OnRetryCallBack | undefined | null
  ): Promise<void> {
    setAuthState(new AuthStateLoading());

    const response: DXTApiRequestState<AuthInfoResult, LoginUserInput> = await userLoginRequest(this.params, app);

    void response.map({
      success: async (successState) => {
        const { user, auth_token } = successState.data;

        setAuthState(new AuthStateLoggedIn(auth_token, user));
      },

      error: async (errorState) => {
        const apiError = errorState.error;

        setAuthState(new AuthStateDisconnected(apiError));

        if (this.errorCallback) this.errorCallback(apiError);
      },
    });
  }
}
