import { type VOInput, ValueObject } from '@/value_objects';

import { isUserName } from '../validation';

export class VOUserName extends ValueObject<string> {

  override validate(rawValue?: VOInput<string>): string {
    if (typeof rawValue === 'string') {
      const value = rawValue.trim();

      if (isUserName(value)) return value;
    }

    this.throwError(rawValue);
  }
  
}
